define('todo-app/router', ['exports', 'ember', 'todo-app/config/environment'], function (exports, Ember, config) {

  'use strict';

  var Router = Ember['default'].Router.extend({
    location: config['default'].locationType
  });

  Router.map(function () {
    this.route("about");
    this.route("favs");
  });

  exports['default'] = Router;

});